@import "dragula/dist/dragula";

.fb-array-items {
  margin: 0 0 2rem 0;
  padding: 0;
}

.fb-array-item {
  position: relative;
  cursor: move;
}

.fb-array-item > div {
  padding: 0.5rem;
  list-style: none;
  border: solid 2px #000;
  border-left-width: 1rem;
  margin-bottom: 0.5rem;
}

.fb-array-item > div:hover {
  background: #f6f6f6;
}

.fb-array-item.gu-mirror {
  height: auto !important;
}

p.fb-array-item--title {
  margin: 0;
}

p.fb-array-item--id {
  margin: 0;
  font-size: 0.75rem;
  line-height: 1;
}

p.fb-array-item--remove {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 1rem;
}

p.fb-array-item--remove span {
  position: absolute;
  left: -9999px;
}

